import React, { Component } from 'react'
import Select from './Select'


const floatDiv={

    float:'left',
    width:'200px',
    height:'200px',
    background:'red',
    marginRight:'5px',
}
export default class Room extends Component {


  render() {
    // let checkbox;
    // let checked='';
    // const {index,onCheck,rooms}=this.props
    // if(!index==0){
    //   if(rooms[index])
    //     checked='checked'
    //   checkbox=<input onChange={()=>onCheck(index)} type='checkbox' checked={checked} ></input>
    // }
      const {id,adultsAgeRange,adultsRange,childrenAgeRange,childrenRange,disable}=this.props

    return (
        <div  style={floatDiv}>

            <p>Room {id} {disable?'disable':'able'}</p>

            <Select id={id} type="adults" ageRange={adultsAgeRange} disable={disable} options={adultsRange} />
            <Select id={id} type="children" ageRange={childrenAgeRange} disable={disable} options={childrenRange} />
        </div>
    )
  }
}
